<template>
  <div id="app">
      <div class="container">
        <!-- Example row of columns -->
        <div class="row">
          <div class="col-md-6">
			<h3>PLAYER</h3>
			<div>
				<div v-bind:style="playerStyle"  >{{playerwins}}</div>
			</div>
          </div>
          <div class="col-md-6">
			<h3>COMPUTER</h3>
			<div>
				<div v-bind:style="computerStyle"  >{{computerwins}}</div>
			</div>
          </div>
        </div>

        <hr>

        <div class="row">
          <div class="col-md-12">
			<button v-on:click="deal('rock')"  >Rock</button>
			<button v-on:click="deal('paper')" >Paper</button>
			<button v-on:click="deal('scissors')" >Scissors</button>
          </div>
        </div>

        <div class="row">
          <div class="col-md-12">
			 <ul>
			   <!-- <li v-for="msg in dealRecords" v-html="msg"  ></li> -->
			   <li v-for="obj in dealRecords" v-html="obj.message" v-bind:class="obj.result" ></li> 
			 </ul>
          </div>
        </div>

      </div> <!-- /container -->

  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
        userClass: '',
        dealRecords: [],
        playerwins: 0,
        computerwins: 0,
        playerStyle: {
        width: 0
        },
        computerStyle: {
        width: 0
        }
    }
  },
  methods: {
    deal: function(hand) {
      console.log('deb');
      var dealobject = this.rps(hand);
      console.log(dealobject);
      //app.dealRecords.push(dealobject.message);
      this.dealRecords.push(dealobject);
      if (dealobject.result == 'player-wins'){
      	this.playerwins += 1;
      	this.playerStyle.backgroundColor = 'red';
      	this.playerStyle.width = this.playerwins + '0' + '%';
      } else 
      if(dealobject.result == 'computer-wins' ){
      	this.computerwins += 1;
      	this.computerStyle.backgroundColor = 'green';
      	this.computerStyle.width = this.computerwins + '0' + '%';
      } 
    },
    rps: function (playerhand) {
      var computerhand;

      var randomNumber = Math.random();
      var message= "";

      if (randomNumber < .33){
        computerhand = 'rock';
      }else if(randomNumber < .66 && randomNumber > .33){
        computerhand = 'paper';
      }else{
        computerhand = 'scissors';
      }
      var combination = playerhand + computerhand;

      var resultIndex;
      var results = ["We tie!","Computer wins","Player wins"];
      var resultsCSS = ["we-tie","computer-wins","player-wins"];
      switch(combination){
        case 'rockrock':
          resultIndex= 0;
          break;
        case 'paperpaper':
          resultIndex= 0;
          break;
        case 'scissorsscissors':
          resultIndex= 0;
          break;
        case 'rockscissors':
          resultIndex= 2;
          break;
        case 'rockpaper':
          resultIndex= 1;
          break;
        case 'scissorsrock':
          resultIndex= 1;
          break;
        case 'scissorspaper':
          resultIndex= 2;
          break;
        case 'paperrock':
          resultIndex= 2;
          break;
        case 'paperscissors':
          resultIndex= 1;
          break;
        default:
          message='I dont recognize that hand';
          break;
      }
      var message = "Player chose " + playerhand + ". Computer played " + computerhand + ". " + results[resultIndex]; 
      //alert(message);
      var dealObject = {};
      dealObject.playerhand = playerhand;
      dealObject.computerhand = computerhand;
      dealObject.message = message;
      dealObject.result = resultsCSS[resultIndex];
      return dealObject;
    }
  },
  watch:{
    computerwins: function(){
       if(app.computerwins == 10){
       		alert('Computer wins match! (Match will be reset)');
       		location.reload();
       }
    },
    playerwins: function(){
       if(app.playerwins == 10){
       		confirm('Player wins match! (Match will be reset) ');
       		location.reload();
       }
    }
  }
}
</script>

<style>
	ul{
	  		list-style: none;
	  		padding: 0;
	}
  	.col-md-6 > div{
  		background-color: silver;
  		width:80%;
  	}
  	.col-md-6 > div > div{
  		  	transition: width 2s;
  		  	text-align:center;
  		  	color: white;
  		  	padding: 5px;
  	}
    .col-md-12{
    	text-align:center;
    }
	.player-wins, .computer-wins, .we-tie{
		color: white;
		margin: 2px;
	}
    .player-wins{
    	background-color:green;
    }
    .computer-wins{
    	background-color:red;
    }
    .we-tie{
    	background-color:blue;
    }
</style>



